import React from 'react';
//Componentes//
import Hero from '../components/Hero';
import ContentHome from '../components/ContentHome';
import ContentHomeTCG from '../components/ContentHomeTCG';
import ContentHomeEnvios from '../components/ContentHomeEnvios';
//Librerias//
import {Helmet} from "react-helmet";
import Zoom from 'react-reveal/Zoom';

function HomePage(props) {

    return(
        <div>
            <Helmet>
                <title>Kuervo's Store ~ Figures and Collectibles</title>
            </Helmet>
            
            <section id="hero" class="hero">
                <div class="hero-contenido">
                    <Hero title={props.title} subTitle={props.subTitle} text={props.text} />
                </div>
            </section>

            <ContentHome>
                <Zoom>
                    <h1 class="display-4 font-weight-light mb-4">BECOME THE ULTIMATE 
                        <h1 class="display-2 font-weight-bolder red-text" >WEEB </h1>
                    AND LOOK AT THE PRE-SALES BELOW!</h1>            
                </Zoom>
            </ContentHome>
            
            <ContentHomeTCG>
                <Zoom>
                    <h1 class="display-4 font-weight-light mt-4">PLAY EVERY SINGLE
                        <h1 class="display-2 font-weight-bolder red-text" > TCG</h1>
                    </h1> 
                    <h1 class="display-4 font-weight-light mt-4">WHILE YOUR 
                        <h1 class="display-2 font-weight-bolder red-text" > WAIFUS</h1>
                    </h1>            
                    <h1 class="display-4 font-weight-light mt-4">PROTECT YOUR 
                        <h1 class="display-2 font-weight-bolder red-text" > DECKS</h1>
                    </h1>
                </Zoom>
            </ContentHomeTCG>
            
            <ContentHomeEnvios />
        </div>
        
    );
}

export default HomePage;