import React from 'react';
//Librerias//
import { AnimateKeyframes } from "react-simple-animate";
import Zoom from 'react-reveal/Zoom';
//Imagenes//
import shipping from '../assets/images/dhl.png';

function ContentHomeEnvios(props) {

    return(
        <section id="home-envios" class="home-envios mt-4">
                <div class="home-envios-contenido mt-4">
                    <Zoom>
                        <h1 class="display-4 font-weight-bolder mt-4">SHIPPING TO ALL MEXICO</h1>
                        <AnimateKeyframes 
                            play={true}
                            iterationCount="infinite"
                            duration={5}
                            keyframes={[
                                "transform: translateX(250px)",
                                "transform: translateX(-250px)"
                            ]}>
                            <img class="d-none d-md-none d-lg-block" alt="shipping" src={shipping} />
                        </AnimateKeyframes>
                        <AnimateKeyframes 
                            play={true}
                            iterationCount="infinite"
                            duration={4}
                            keyframes={[
                                "transform: translateX(180px)",
                                "transform: translateX(-100px)"
                            ]}>
                            <img class="d-none d-md-block d-lg-none" alt="shipping" src={shipping} />
                        </AnimateKeyframes>
                        <AnimateKeyframes 
                            play={true}
                            iterationCount="infinite"
                            duration={3}
                            keyframes={[
                                "transform: translateX(100px)",
                                "transform: translateX(-50px)"
                            ]}>
                            <img class="d-none d-sm-block d-md-none" alt="shipping" src={shipping} />
                        </AnimateKeyframes>
                        <AnimateKeyframes 
                            play={true}
                            iterationCount="infinite"
                            duration={3}
                            keyframes={[
                                "transform: translateX(100px)",
                                "transform: translateX(-80px)"
                            ]}>
                            <img class="xsm-image d-block d-sm-none" alt="shipping" src={shipping} />
                        </AnimateKeyframes>
                        <p>If you're interested in some products</p>
                        <p>Just contact us clicking the <p class="display-3 font-weight-bolder">Messenger Chat!</p></p>
                        <p>And we'll attending you.</p>
                    </Zoom>
                </div>
            </section>
    );

}

export default ContentHomeEnvios;