import React from 'react';
//Componentes//
import Hero from '../components/Hero';
//Librerias//
import {Helmet} from "react-helmet";
import Zoom from 'react-reveal/Zoom';
//Imagenes//
import constructionCheems from '../assets/images/cheems-construccion.png';

function NotFound(props) {

    return(
        <div>
            <Helmet>
                <title>Kuervo's Store ~ Section In Construction</title>
            </Helmet>
            
            <section id="not-found-hero" class="not-found-hero">
                <div class="not-found-contenido">
                    <Hero title={props.title} />
                        <img class="not-found-image" alt="contruction" src={constructionCheems} />
                    <Hero subTitle={props.subTitle} text={props.text} />
                </div>
            </section>

        </div>
        
    );
}

export default NotFound;